import styled from 'styled-components'

/**
 * [BaseContent 基本的内容布局组件]
 * @type {[type]}
 */
const BaseContent = styled.div`
  max-width: 1080px;
  margin: 0 auto;
  padding: 60px 10px;
  text-align: left;
  overflow: hidden;
`

export default BaseContent



